import React from 'react';
import classes from './WorkWithAPI.module.css';
import { setUsersAC, requestUsers, setCurrentPageAC } from '../../../redux/users-reducer';
import { getUsers, getPageSize, getTotalUsersCount, getCurrentPage } from '../../../redux/users-selector';
import { connect } from 'react-redux';
import { useEffect } from 'react';
import userPhoto from '../../../accets/img/nophoto.png';
import Paginator from '../../common/Paginator/Paginator';

const WorkWithAPI = (props) => {

  useEffect(() => {
    props.setCurrentPage(props.currentPage);
    props.getUsersDispatch(props.currentPage);
  }, [props.currentPage]);

  let onPageChanged = (pageNumber) => {
    props.getUsersDispatch(pageNumber, props.pageSize);
  }

  return (
    <div>
      <div className={classes.users}>
        {props.users.map(u =>
          <div className={classes.users__block}>
            <img src={u.photos.small != null ? u.photos.small : userPhoto} alt="" />
            <div className="">
              <strong>ID:</strong> {u.id} <br></br>
              <strong>NAME:</strong> {u.name} <br></br>
              <strong>STATUS:</strong> {u.status !== null ? u.status : 'no status'}
            </div>
          </div>
        )
        }
      </div>
      <Paginator currentPage={props.currentPage} onPageChanged={onPageChanged}
        totalItemsCount={props.totalUsersCount} pageSize={props.pageSize} />
    </div>
  )
}

let mapStateToProps = (state) => {
  return {
    users: getUsers(state),
    pageSize: getPageSize(state),
    totalUsersCount: getTotalUsersCount(state),
    currentPage: getCurrentPage(state)
  }
}

let mapDispatchToProps = (dispatch) => {
  return {
    setUsers: (users) => {
      dispatch(setUsersAC(users));
    },
    setCurrentPage: (currentPage) => {
      dispatch(setCurrentPageAC(currentPage))
    },
    getUsersDispatch: (currentPage) => {
      dispatch(requestUsers(currentPage))
    }
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(WorkWithAPI);