import React from 'react';
import classes from './App.module.css';
import About from './components/About/About';
import Navbar from './components/Navbar/Navbar';
import { Route } from 'react-router-dom';
import MainPage from './components/MainPage/MainPage';
import Portfolio from './components/Portfolio/Portfolio';
import Game2048 from './components/Portfolio/2048/Game2048';
import WorkWithAPI from './components/Portfolio/WorkWithAPI/WorkWithAPI';

const App = () => {
  return (
    <div className={classes.s}>
      <div className={classes.app}>
        <Navbar />
        <div className={classes.app__content}>
          <Route exact path="/" render={() => <MainPage />} />
          <Route exact path="/portfolio" render={() => <Portfolio />} />
          <Route exact path="/portfolio/game2048" render={() => <Game2048 />} />
          <Route exact path="/portfolio/API" render={() => <WorkWithAPI />} />
        </div>
        <About />
      </div>
    </div>
  );
}

export default App;
