import React from 'react';
import classes from './Navbar.module.css';
import { NavLink as Link } from 'react-router-dom';

const Navbar = () => {
  return (
    <div className={classes.navbar}>
      <Link className={classes.item} exact to="/" activeClassName={classes.active} >MAIN<br></br>PAGE</Link>
      <Link className={classes.item} to="/some" activeClassName={classes.active} >RES<br></br>UME</Link>
      <Link className={classes.item} to="/portfolio" activeClassName={classes.active} >PORT<br></br>FOLIO</Link>
      <Link className={classes.item} to="/some3" activeClassName={classes.active} >CON<br></br>TACTS</Link>
    </div>
  )
}

export default Navbar;